/**
* Basic icon styles. Allows you to easily render sprite icons.
*
* .aui-icon must be present on all iconified elements
* All iconified elements must include class names prefixed with 'aui-icon-'
* Icons assume light backgrounds. Icon classnames for dark backgrounds must be suffixed with '-d'
* For elements whose background changes in app, we advise maintaining light/dark state in the app.
*
*/
.aui-icon {
  background-repeat: no-repeat;
  background-position: 0 0;
  border: none;
  display: inline-block;
  height: 16px;
  margin: 0;
  padding: 0;
  text-align: left;
  text-indent: -999em;
  vertical-align: text-bottom;
  width: 16px;
}
.aui-icon-small,
.aui-icon-large {
  line-height: 0;
  position: relative;
  vertical-align: text-top;
}
.aui-icon-small::before,
.aui-icon-large::before {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0;
  font-family: "Adgs Icons";
  font-weight: normal;
  font-style: normal;
  speak: none;
  color: inherit;
  left: 0;
  line-height: 1;
  position: absolute;
  text-indent: 0;
  top: 50%;
}
.aui-icon-small {
  height: 16px;
  width: 16px;
}
.aui-icon-large {
  height: 32px;
  width: 32px;
}
.aui-icon-small:before {
  font-size: 16px;
  margin-top: -8px;
}
.aui-icon-large:before {
  font-size: 32px;
  margin-top: -16px;
}
